@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
#menu{
    display: flex;
    height: 100%;
}

#menu a{
    margin-left: 1rem;
    
    height: 100%;

    color: var(--white);

    font-weight: 700;

    text-transform: capitalize;
}

#menu a:hover{
    color: var(--primary-color);
}
.navbar{
    display: flex;
    width: 100%;
    margin: 0;
    padding: 0;

    align-items: center;
    justify-content: space-between;

    background-color: var(--bg-dark);
    height: 10vh;
    padding: 0 2rem;
}

.navbar img{
    transform: translateY(10%);
}
.container{
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
}
.home-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

#Home{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 4rem;
    
}

.home-section{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 2rem;
    align-items: flex-start;
    justify-content: center;
    min-height: 80vh;
}
.home-section header{
    display: flex;
    flex-direction: column;
}
.home-section header p{
    font-weight: 600;
    text-align: left;
}
.home-section header p b{
    font-weight: 400;
    color: var(--primary-color);
}
.home-section img{
    width: 100%;
}
.home-buttons-container{
    margin-top: 1rem;
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: flex-start;
}
.home-btn{
    background-color: transparent;
    border: none;
    height: 3rem;
    width: 8rem;
    border-radius: 6px;
    font-weight: 700;
    text-transform: capitalize;

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;

    transition: ease .2s;
}
.home-btn:first-of-type{
    background-color: var(--secondary-color);
    border: 2px solid var(--secondary-color);
    color: var(--white);
    margin-right: 1rem;
}
.home-btn:first-of-type:hover{
    background-color: transparent;
    border: 2px solid var(--secondary-color);
    color: var(--secondary-color);
}
.home-btn:last-of-type{
    background-color: var(--white);
    border: 2px solid var(--secondary-color);
    color: var(--secondary-color);
}
.home-btn:last-of-type:hover{
    background-color: var(--secondary-color);
    border: 2px solid var(--secondary-color);
    color: var(--white);
}
#home-footer{
    height: 100%;
    width: 100%;
    background-color: var(--bg-dark);
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: var(--white);
}
.footer-section{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 0 2rem;
}
.footer-section div{
    height: 100%;
    display: flex;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.footer-section div a{
    color: var(--white);
}

.footer-section div p{
    font-weight: 100;
}
@media screen and (max-width: 600px) {
    #Home{

        padding: 0;
    }
    .home-section{
        padding: 0;
        margin: 0;
    }
    .footer-section{
        padding: 0;
    }
}
#Wallet{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: flex-end;
}

#Wallet > header{
    margin-top: 2rem;
    margin-bottom: 5rem;
    width: 100%;

    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;
}

#Wallet > header p{
    font-weight: 500;
}
#Wallet > header h2{
    font-size: 2rem;
    font-weight: 900;
}
.wallet-amount{
    color: var(--primary-color);
    font-weight: 600 !important;
}

.wallet-transactions{
    height: 100%;
    display: flex;
    flex-direction: column;

    align-items: center;

    width: 70%;
}

.wallet-transactions > header{
    display: flex;
    border-bottom: 3px solid var(--dark-gray);
    width: 100%;
    align-items: center;
    justify-content: center;
    
    color: var(--dark-gray);
    margin-bottom: 1rem;
}
.wallet-transactions > header div{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.wallet-transactions > header div:first-of-type{
    margin-right: 5rem;
    height: 100%;
    font-weight: 700;

}

.wallet-transactions > header div.active{
    color: var(--secondary-color);
}

.search-transactions{
    width: 100%;
}

.search-transactions input{
    width: 100%;
    height: 2rem;
    outline: none;

    border-radius: 6px;
    padding: .5rem;

    border: none;

    background-color: var(--light-gray);
}

.transactions-history{
    width: 100%;

    margin-top: 2rem;
}

.transaction-card{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--light-gray);
    margin-bottom: .5rem;

    border-radius: 6px;

    padding: 1rem 2rem;
}

.transaction-card-section{
    display: flex;

    height: 100%;

    color: var(--dark-gray)
}

.transaction-card-section img{
    width: 3rem;
    height: auto;
    margin-right: 2rem;
}
#Buy{
    height: 100%;
    width: 100%;

    display: flex;

    align-items: center;
    justify-content: center;
    padding: 0 10rem;
}
.buy-section{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    justify-content: center;
}
.payment{
    height: 100%;
    display: flex;
    flex-direction: column;   
}

.payment > header{
    display: flex;
    margin: 2rem 0;

}
.payment > header p{
    margin-right: 1rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.payment > header p:hover,
.payment > header p.active{
    color: var(--secondary-color);
    border-bottom: 2px solid var(--secondary-color);
}
.payment-inputs{
    display: flex;
    flex-direction: column
}
.payment-inputs input{
    height: 2rem;
    padding: 0 1rem;
}
.payment-inputs input:first-of-type{
    margin-bottom: 1rem;
}
.payment-inputs input:last-of-type{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    pointer-events: none;
}
.payment-inputs button{
    transition: ease .2s;
    height: 2rem;
    width: 8rem;
    background-color: var(--primary-color);
    border: 2px solid var(--primary-color);
    color: var(--white);
    border-radius: 6px;
    margin-top: 4rem;
    cursor: pointer;
}
.payment-inputs button:hover{
    background-color: transparent;
    color: var(--primary-color);
}
.credit-cards{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;

    align-items: center;
}

.credit-cards img{
    width: 30rem;
}
#Total-staked{
    height: 100%;
    width: 100%;

    display: flex;

    flex-direction: column;

    align-items: center;
    justify-content: center;

    padding: 0 10rem;
    background-color: rgba(255, 255, 255, 0.5);
}

.stake-container{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.stake-container header{
    display: flex;
    align-self: flex-start;
    justify-self: self-start;
    
    align-items: center;
    justify-content: center;
    background-color: var(--light-gray);
    padding: 0 2rem;
    border-radius: 6px 6px 0 0;
}
.stake-container header img{
    margin-left: 1rem;
}
.stake-container header p{
    margin: 0 1rem;
}
.stake-amount{
    border-radius: 0 6px 0px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background-color: var(--light-gray);
    position: relative;
}

.stake-amount h1{
    font-size: 3rem;
